/* eslint-disable */
import { useNavigate } from "react-router-dom";
import React, { useState, useRef } from "react";
import axios from "axios";

function Signup() {
  // id, pw, pw  
  let [id, setId] = useState("");
  let [pw, setPw] = useState("");
  let [pwchk, setPwchk] = useState("");

  //   
  let [pwmessage, setPwmessage] = useState("");

  // 
  let [ispwconfirm, setIspwconfirm] = useState(true);
  let [idchk, setIdchk] = useState(false);

  const navigate = useNavigate();

  // id  
  const idHandler = (e) => {
    e.preventDefault();
    setId(e.target.value);
  };

  // pw  
  const pwHandler = (e) => {
    e.preventDefault();
    setPw(e.target.value);
  };

  //  
  const pwConfirm = (e) => {
    const passwordConfirm = e.target.value;
    setPwchk(passwordConfirm);

    if (pw === passwordConfirm) {
      setPwmessage(" .    .");
      setIspwconfirm(false);
    } else {
      setPwmessage("  . ");
      setIspwconfirm(true);
    }
  };

  //  
  const submitHandler = (e) => {
    e.preventDefault();
    let body = {
      id: id,
      // pw: btoa(pw),
      pw: pw,
    };
    {
      idchk
        ? axios.post("api/signup", body).then((res) => {
            {
              //         
              res.data == "" ? alert("      .") : navigate("/");
            }
          })
        : alert(" ID   ");
    }

    console.log("  " + id);
    console.log("  " + pw);
  };

  async function CHECK_ID() {
    let body = {
      id: id,
    };
    try {
      await axios.post("api/signup/checkID", body).then((res) => {
        console.log(" : " + res.data);
        {
          res.data === "" ? alert("  .") : alert("  .");
        }
      });
    } catch (err) {
      console.log(err);
    }
  }
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-light">
        <div className="container-fluid">
          <a className="navbar-brand m-3" href="/">
            KWIC_WEB Shop 
          </a>
        </div>
      </nav>
      <h4 className="container mt-5 col-6"></h4>
      <div className="container mt-3 col-6 mx-auto">
        <form onSubmit={submitHandler}>
          {/*   */}
          <div className="form-group">
            <label>ID</label>
            <div>
              <input type="text" className="form-control" value={id} onChange={idHandler} placeholder="  ."></input>
            </div>
            <button
              className="btn btn-dark mt-3 d-grid gap-2 col-3 mx-auto"
              onClick={(e) => {
                e.preventDefault();
                CHECK_ID();
                setIdchk(true);
              }}
            >
              
            </button>
          </div>
          {/*   */}
          <label>Password</label>
          <input
            type="password"
            className="form-control"
            value={pw}
            onChange={pwHandler}
            onClick={(e) => {
              e.preventDefault(e);
            }}
            placeholder=" "
          ></input>
          {/*   */}

          <div className="form-group mt-3">
            <label>Password </label>
            <input type="password" className="form-control" placeholder=" " onChange={pwConfirm} />
            {pwchk.length > 0 && <span>{pwmessage}</span>}
          </div>
          {/*   */}
          <button onSubmit={submitHandler} className="btn btn-primary mt-5 d-grid gap-2 col-6 mx-auto" type="submit" disabled={ispwconfirm}>
             
          </button>
        </form>
      </div>
    </>
  );
}

export default Signup;
